import About from './components/About.vue'

import Brands from './components/Brands.vue'
const router = VueRouter.createRouter({
    history: VueRouter.createWebHistory(),
    routes: [
        {
        path: '/',
        name: 'Home',
        component: Brands
        },
        {
            path: '/about',
        name: 'About',
        component: About
        }
      ]
    })
    


createApp(App).use(router)
  .use(Vue3Material)
.mount('#app')

